//global style
@font-face {
	font-family: AraJozoor;
	src: url(../webfonts/AraJozoor-Regular.otf);
}

.main-wrapper {
	width: 100%;
	overflow: hidden;
}

body {
	font-family: "AraJozoor", sans-serif;

	.main-wrapper {
		overflow-x: hidden;
		background-image: url(../images/bg.png);
		background-position: left;
		background-repeat: repeat;
		background-size: cover;
		position: relative;
		width: 100%;
		min-height: 100vh;
	}
}
//global style

//main section
section.main {
	padding: $padding_section;

	.logo {
		max-width: 70px;
		margin-bottom: 30px;
	}

	.main-title {
		text-align: center;
		color: #fff;
	}

	.content {
		.box {
			text-align: center;
			overflow: hidden;
			margin-bottom: 30px;

			&:hover {
				.roll-down {
					margin-top: 0;
				}
			}

			.name {
				color: $main_color;
				background-color: #fcfcfc;
				border-radius: 50px;
				padding: 7px 15px;
				border: 1px solid $main_color;
				display: inline-block;
				position: relative;
				z-index: 2;
				cursor: pointer;
				width: 320px;
				max-width: 100%;
			}

			.roll-down {
				// visibility: hidden;
				margin-top: -70px;
				z-index: 1;
				position: relative;
				transition: all ease 0.2s;

				.icons {
					padding: 15px;
					background-color: #fcfcfc;
					display: inline-block;
					border-bottom: 4px solid $main_color;
				}

				a {
					display: inline-block;
					margin: 0 15px;
				}

				img {
					max-width: 50px;
				}
			}
		}
	}
}

//main section

//footer
footer {
	padding: 50px 0;
	background-color: transparent;

	.content {
		display: flex;
		justify-content: space-between;
		align-items: center;

		.footer-logo {
			width: 200px;
		}
	}

	.footer-companies {
		ul {
			padding: 0;
			margin: 0;

			li {
				list-style: none;
				margin-bottom: 10px;
				display: flex;

				.icon {
					width: 25px;
					height: 25px;
					border-radius: 50%;
					background-color: #fff;
					margin-inline-end: 10px;
					@extend %center;
				}

				img {
					width: 20px;
				}

				&:last-of-type {
					margin-bottom: 0;
				}

				a {
					color: #fff;
					transition: all ease 0.3s;

					&:hover {
						color: $main_color;
					}
				}
			}
		}
	}
}

.copyrights {
	background-color: $main_color;
	padding: 15px 0;
	text-align: center;

	p {
		margin-bottom: 0;
		color: $second_color;
	}
}

//footer
